<template>
    <div id="">
        <md-card class="mycard">
            <md-card-header >
                <div class="md-title">App shortcuts sample</div>
                <div class="md-subhead">This sample demonstrates how to use the app shortcuts API introduced in Android 7.1 (API level 25). This API allows an application to define a set of intents which are displayed when a user long-presses on the app's launcher icon. Examples are given for registering links both statically in XML, as well as dynamically at runtime. </div>
            </md-card-header>
    
        </md-card>
        <md-card class="mycard">
            <md-card-header >
                <div class="md-title">Image keyboard app sample</div>
                <div class="md-subhead">This sample demonstrates how to implement the Commit Content API, using the Android Support Library. This API provides a universal way for IMEs to send images and other rich content directly to a text editor in an app, allowing users to compose content using custom emojis, stickers, or other rich content provided by other applications.</div>
         </md-card-header>
    
        </md-card>
        <md-card class="mycard">
            <md-card-header >
                <div class="md-title">Image keyboard IME sample</div>
                <div class="md-subhead">This sample demonstrates how to write a custom image keyboard using the Commit Content API and the Android Support Library. This keyboard will be displayed inside compatible apps (also using the Commit Content API), allowing users to insert emojis, stickers, or other rich content into text editors.</div>
            </md-card-header>
    
        </md-card>
      
    </div>
</template>

