            <md-card-header>
                <div class="md-title">BasicSyncAdapter</div>
                <div class="md-subhead">This sample demonstrates using SyncAdapter to fetch background data for an app that doesn\'t require a user-visible account type or 2-way synchronization. \n\nThis sample periodically downloads the feed from the Android Developer Blog and caches the data in a content provider. At runtime, the cached feed data is displayed inside a ListView.</div>
            </md-card-header>
    
