<template>
    <div id="">
        <md-card class="mycard">
            <md-card-header >
                <div class="md-title">AppRestrictionEnforcer</div>
                <div class="md-subhead">This sample demonstrates how to set restrictions to other apps as a profile owner. Use the AppRestrictionSchema sample to set restrictions.</div>
            </md-card-header>
    
            <md-card-actions>
                <a href="/static/AppRestrictionEnforcer.zip"><md-button class="md-raised md-primary">下载</md-button></a>
            </md-card-actions>
        </md-card>
        <md-card class="mycard">
            <md-card-header>
                <div class="md-title">AppRestrictionSchema</div>
                <div class="md-subhead">This sample shows how to use app restrictions. This application has one boolean restriction with a key \"can_say_hello\" that defines whether the only feature of this app (press the button to show \"Hello\" message) is enabled or disabled. Use AppRestrictionEnforcer sample to toggle the restriction.</div>
            </md-card-header>
    
            <md-card-actions>
                 <a href="/static/AppRestrictionSchema.zip"><md-button class="md-raised md-primary">下载</md-button></a>
            </md-card-actions>
        </md-card>
        <md-card class="mycard">
            <md-card-header>
                <div class="md-title">BasicManagedProfile</div>
                <div class="md-subhead">This sample demonstrates how to create a managed profile. You can also learn how to enable or disable other apps and how to set restrictions to them. Intents can be configured to be forwarded between primary account and managed profile. Finally, you can wipe all the data associated with the profile. Note that there can only be one managed profile on a device.</div>
            </md-card-header>
    
            <md-card-actions>
                <a href="/static/BasicManagedProfile.zip"><md-button class="md-raised md-primary">下载</md-button></a>
            </md-card-actions>
        </md-card>
        <md-card class="mycard"> 
            <md-card-header>
                <div class="md-title">DeviceOwner</div>
                <div class="md-subhead">This app demonstrates how to use device owner features. Use the NfcProvisioning sample to set up this app as the device owner of your test device (This requires wiping out the device and resseting it as an unprovisioned device). As a device owner, this app can configure global settings, and enforce use of a specific launcher.</div>
            </md-card-header>
    
            <md-card-actions>
                <a href="/static/DeviceOwner.zip"><md-button class="md-raised md-primary">下载</md-button></a>
            </md-card-actions>
        </md-card>
        <md-card class="mycard">
            <md-card-header>
                <div class="md-title">NfcProvisioning</div>
                <div class="md-subhead">This sample demonstrates how to provision other devices with a specified device owner using NFC. Install the DeviceOwner sample to an unprovisioned device. Fill in the values below, and bump the two devices, and tap this side to initiate the provisioning process.</div>
            </md-card-header>
    
            <md-card-actions>
                 <a href="/static/NfcProvisioning.zip"><md-button class="md-raised md-primary">下载</md-button></a>
            </md-card-actions>
        </md-card>
    </div>
</template>

