<template>
    <div id="">
        <md-card class="mycard">
            <md-card-header >
                <div class="md-title">AppShortcuts</div>
                <div class="md-subhead">This sample demonstrates how to use the Launcher Shortcuts API introduced in API 25. This API allows an application to define a set of Intents which are displayed as when a user long-presses on the app's launcher icon. Examples are given for registering both links both statically in XML, as well as dynamically at runtime.</div>
            </md-card-header>
    
            <md-card-actions>
                <a href="/static/AppShortcuts.zip"><md-button class="md-raised md-primary">下载</md-button></a>
            </md-card-actions>
        </md-card>
        <md-card class="mycard">
            <md-card-header>
                <div class="md-title">AppUsageStatistics</div>
                <div class="md-subhead">This sample explains how to use App usage statistics API, which was introduced in Android 5.0.</div>
            </md-card-header>
    
            <md-card-actions>
               <a href="/static/AppUsageStatistics.zip"> <md-button class="md-raised md-primary">下载</md-button></a>
            </md-card-actions>
        </md-card>
        <md-card class="mycard">
            <md-card-header >
                <div class="md-title">RuntimePermissions</div>
                <div class="md-subhead">This sample shows runtime permissions available in Android M and above. Display the log on screen to follow the execution. If executed on an Android M device, an additional option to access contacts is shown that is declared with optional, M and above only permissions.</div>
            </md-card-header>
    
            <md-card-actions>
                <a href="/static/RuntimePermissions.zip"><md-button class="md-raised md-primary">下载</md-button></a>
            </md-card-actions>
        </md-card>
        <md-card class="mycard">
            <md-card-header>
                <div class="md-title">RuntimePermissionsBasic</div>
                <div class="md-subhead">This sample shows a basic implementation for requesting permissions at runtime. Click the button to request the Camera permission and open a full-screen camera preview. Note: The "RuntimePermissions" sample provides a more complete overview over the runtime permission features available.</div>
            </md-card-header>
    
            <md-card-actions>
              <a href="/static/RuntimePermissionsBasic.zip">  <md-button class="md-raised md-primary">下载</md-button></a>
            </md-card-actions>
        </md-card>
    </div>
</template>

<script>
export default {

}
</script>

<style>
.md-title {
    text-align: left;
}

.md-subhead {
    text-align: left;
    font-size: 25px;
}

.mycard{
    margin:20px 20px
}

.md-card .md-subhead {
    margin-top: 10px;
    opacity: .99;
    font-size: 16px;
    letter-spacing: .01em;
    line-height: 20px;
}
</style>



